.content {
    display: grid;
    grid-template-columns: 1fr 2fr;
}

.ImageAvatarka {
    display: grid;
    grid-template-rows: 2fr 200px 1fr;
    border: 1px solid black;
    text-align: center;
    font-family: "SW Crawl Title";
    /*font-family: "Segoe UI";*/
    color: goldenrod;
    font-size: 40px;

}

.ImageAvatarka img {
    margin-top: 50px;
    width: 200px;
    border-radius: 50%;
}

.Right {
    border: 1px solid black;
}

.status {
    border-bottom: 1px solid black;
    text-align: center;
    color: black;
    font-size: 16px;
    margin-top: 20px;
    margin-bottom: 40px;
    padding-bottom: 20px;
}

.status span span {
    font-family: "Monotype Corsiva";
    font-size: 28px;
    color: goldenrod;
}

.aboutMe {
    font-size: 22px;
    color: rgb(171, 119, 240);
}

.contacts {
    text-align: center;
    color: black;
    font-size: 16px;
}

.contacts p {
    color: rgb(171, 119, 240);
    font-size: 16px;
}



